import React, { Component } from 'react';
import styled from 'styled-components';
import NavigationKo from '../Layout/NavigationKo';

class 연구분야 extends Component {
  render() {
    return (
      <Layout>
      <NavigationKo />
      <Square>
        <div>
          <h1>연구분야</h1>
          <dl>
            <dd><b>2009.10 -현재 </b>유기질 비료, 사료첨가제, 토질개량제등 농업기술개발</dd>
            <dd><b>2009.10 - 현재 </b>독일 Gicon사의 Biogas 처리기술 최적화 참여.</dd>
            <dd><b>2007.5 - 2008.4</b> 누에동충하초를 이용한 당뇨증세 완화 및 치료용 기능성 식품 개발</dd>
            <dd><b>2007.3 - 2008.2</b> Biogas 반응기 디자인(중국 UNEP 과제로 돼지농장폐기물 발효용 반응기)</dd>
            <dd><b>2003년 -2005년</b> Biosurfactant를 이용한 VOC 처리 Biofiltersystem 개발(환경생물공학분야)</dd>
            <dd><b>2003년 - 2003년 </b>Itaconic acid 대량생산기술개발(환경생물공학)</dd>
            <dd><b>1999년 - 2001년 </b>치커리폐기물로 부터 DFAIII의 대량생산기술개발(환경생물공학)</dd>
            <dd><b>1998년 - 2000년 </b>식품가공폐기물을 이용한 젖산칼슘의 대량생산기술공정개발(환경생물공학)</dd>
            <dd>치커리로 부터 식품감미제의 대량생산기술공정개발(식품생물공학)</dd>
            <dd>효모세포벽으로부터 β-Glucan의 대량생산기술공정개발(생물공정공학)</dd>
            <dd>제주도 감귤폐기물로부터 펙틴성분의 대량생산기술공정개발(환경생물공학)</dd>
            <dd><b>1992년 - 1998년 </b>미생물세포를 활용한 식품첨가물 및 사료첨가제의 생산(환경 및 사료생물공학)</dd>
            <dd><b>1991년 - 1992년</b> 광합성 세균에 의한 고농도 유기폐수처리공정 및 부산물의 자원화(환경 및 사료생물공학)</dd>
            <dd><b>1990년 - 1990년</b> UNIDO Project, Industrilization of lactic acid fermentation technology of cereals and its dissemination to developing countries</dd>
            <dd><b>1985년 - 1988년 </b>Saccharomyces cerevisiae의 대량생산기술의 효율화</dd>
            <dd><b>1985년 - 1985년</b> Rous Sarcoma Virus로 부터 partial gene의 분리와 동정</dd>
            <dd><b>1985년 - 1985년 </b>Neurospora crassa의 mitochondrial innermembrane에서 porin의 분리정제 및 결정화</dd>
            <dd><b>1984년 - 1985년</b> E. coli의 각 growth phase에 따른 RNA함량의 변화</dd>
            <dd><b>1983년 - 1984년 </b>변이균주를 이용한 공업폐수처리</dd>
            <dd><b>1982년 </b>식품첨가물의 개발 및 연구</dd>

          </dl>
        </div>
      </Square>
      <Square>
        <div>
          <h1>특허현황</h1>
          <ul>
            <li>당밀 희석액 또는 과일, 야채 가공폐기물을 이용한 유산칼슘의 제조방법, 출원인코드 4-2000-015048-0</li>
            <li>치커리 뿌리를 이용한 디에프에이쓰리의 생산방법(제99-54387호)</li>
            <li>토질개선물질과 토질개선제 생산기술, 출원 P08897DE-MA, 독일, 2011년 6월1일</li>

          </ul>
        </div>
      </Square>
      </Layout>
    );
  }
}


const Layout = styled.div`
  width: 100%;
  hight: 100%;
  flex: 1;
  flexDirection: column;
  alignItems: flex-start;
  text-align: center;
  padding: 5vw;
`

const Square = styled.div`
    order: 1;
    width: 86%;
    text-align: left;
    background-color: rgba(256, 256, 256, 0.8);
    padding-left: 2vw;
    padding-right: 2vw;
    padding-top: 1px;
    padding-bottom: 1px;
    line-height: 150%;
    margin-top: 30px;
`

export default 연구분야;