import React, { useState } from 'react';
import './NavigationEn.css';
import { Button } from 'antd';
import MenusKo from '../Components/MenusKo';
import MenusWebKo from '../Components/MenusWebKo';
import MenusClosed from '../Components/MenusClosed';

function NavigationKo() {

    const [viewCalendar, setViewCalendar] = useState(false)
  
    return (
      <div className="contentWrapper">
        <MenusWebKo/>
        <div className="mainComponentWrapper">
          { viewCalendar ? <MenusKo/> : <MenusClosed/> }
        </div>
        <div className="mainButtonWrapper">
          <Button className='Button'
            onClick={() => setViewCalendar(!viewCalendar)}
          ><img width="20px" height="15px" src="img/toggle.png" alt="toggle"/></Button>
        </div>
      </div>
    );
  }
  
  export default NavigationKo;

