import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Menus.css';

class Menus extends Component {
    render() {
      return (
        <nav className='navBar'>
            <p className='navBar_menus'>
                <p className='navBar_menus_menu'>
                    <Link to='/' style={{ textDecoration: 'none', color: 'white' }}>Home</Link>
                </p>
                <p className='navBar_menus_menu'>
                    <Link to='/personaldata' style={{ textDecoration: 'none', color: 'white' }}>Personal Data</Link>
                </p>
                <p className='navBar_menus_menu'>
                    <Link to='/carrier' style={{ textDecoration: 'none', color: 'white' }}>Carrier</Link>
                </p>
                <p className='navBar_menus_menu'>
                    <Link to='/projects' style={{ textDecoration: 'none', color: 'white' }}>Projects</Link>
                </p>
                <p className='navBar_menus_menu'>
                    <Link to='/publications' style={{ textDecoration: 'none', color: 'white' }}>Publications</Link>
                </p>
                
            </p>
                
        </nav>
        
      );
    }
  }
  
export default Menus;
