import React, { Component } from 'react';
import styled from 'styled-components';
import Header from '../Layout/Header';
import Navigation from '../Layout/Navigation';

class Home extends Component {
  render() {
    return (
        <Layout>
            <Header />
            <Navigation />
        </Layout>
    );
  }
}

const Layout = styled.div`
  width: 100%;
  hight: 100%;
`


export default Home;