import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Home, PersonalData, Carrier, Projects, Publications, PersonalDataKo, CarrierKo, ProjectsKo, PublicationsKo } from './index';

const Router = () => (
    <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/personaldata" component={PersonalData} />
        <Route path="/carrier" component={Carrier} />
        <Route path="/projects" component={Projects} />
        <Route path="/publications" component={Publications} />
        
        <Route path="/소개" component={PersonalDataKo} />
        <Route path="/활동" component={CarrierKo} />
        <Route path="/연구분야" component={ProjectsKo} />
        <Route path="/논문발표" component={PublicationsKo} />
    </Switch>
);

export default Router;