import React, { Component } from 'react';
import styled from 'styled-components';
import NavigationEn from '../Layout/NavigationEn';

class Projects extends Component {
  render() {
    return (
      <Layout>
      <NavigationEn />
      <Square>
        <div>
          <h1>Scientific carrier</h1>
          <ul>
            <li>Fertilizer from biowaste after biogas-production</li>
            <li>Soil conditioner with microorganisms</li>
            <li>Biogas production process from variable biowaste</li>

          </ul>
        </div>
      </Square>
      <Square>
        <div>
          <h1>Scientific carrier</h1>
          <ul>

            <li>Lysimeter application in variable soil samples </li>
            <li>VOC gas treatment with biosurfactants in the biofiltersystem</li>
            <li>Evaluation of environmental influence in the rural waste water</li>
            <li>Pharmaceuticals and cosmetic substance with biosurfactants  </li>
            <li>Pharmaceutical, neutraceutical and cosmeceutical substance with Cyanobacteria</li>
            <li>Feedsuppliment-development with Spirulina platensis from swine waste </li>
            <li>Feed-development for marbling effect in meat texture </li>
            <li>Utilization of waste after Monascus sp. cultivation in corn </li>
            <li>Leachate treatment with Phanerochaete chrysosporium </li>
            <li>Removal of Endosulfan with Phanerochaete chrysosporium </li>
            <li>Purification of beta-glucan from Yeast cell wall </li>
            <li>Production of clinical pectin from korean mandarine </li>
            <li>Production of DFAIII from chicory root extract with recombinant  E.coli </li>
            <li>Produciton of soluble organic minerals from variable food manufacturing wastes. </li>
            <li>Production process of itaconic acid </li>


          </ul>
        </div>
      </Square>
      <Square>
        <div>
          <h1>Patents</h1>
          <ul>
            <li>New soil conditioner production technology, P08897 DE-MA, Germany, 20110601</li>
            <li>Production of Organic Calcium Preparates from Molasses or Food, Vegetable Manufacturing Wastes, Korean code number 4-2000-015048-0</li>
            <li>DFAIII Production Process from Chicory, Korean code number 99-54387</li>

          </ul>
        </div>
      </Square>
      </Layout>
    );
  }
}

const Layout = styled.div`
  width: 100%;
  hight: 100%;
  flex: 1;
  flexDirection: column;
  alignItems: flex-start;
  text-align: center;
  padding: 5vw;
`

const Square = styled.div`
    order: 1;
    width: 86%;
    text-align: left;
    background-color: rgba(256, 256, 256, 0.8);
    padding-left: 2vw;
    padding-right: 2vw;
    padding-top: 1px;
    padding-bottom: 1px;
    line-height: 150%;
    margin-top: 30px;
`
export default Projects;