import React, { Component } from 'react';
import styled from 'styled-components';

class Header extends Component {
    render() {
      return (
        <Container>
            <Element>
                <Emptyh>

                </Emptyh>
                
                <Logo>
<img
width="100%"
height="100%"
src="img/kimgieun.png"
alt="logo"/>
</Logo>
                <Search><h1>Kim, Gi Eun</h1></Search>
                <Search2><h2>Professor, Consultant for Industry Projects</h2></Search2>
            </Element>

            
        </Container>
        
      );
    }
  }
  
export default Header;

const Container = styled.div`
    width: 100%;
    display: flex;
    flex-flow: row wrap;
`

const Element = styled.div`
    margin: 0 auto;
    width: 100%;
    height: 600px;
    display: flex;
    flex-flow: row wrap;
`

const Emptyh = styled.div`
    order: 1;
    width: 100%;
    height: 20%;
    text-align: right;
`

const Logo = styled.div`
    order: 2;
    width: 200px;
    height: 200px;
    margin: auto;
    display: block;
`

const Search = styled.div`
    order: 3;
    width: 100%;
    height: 70px;
    text-align: center;
    margin: auto;
    display: block;
    font-size: 1.5em;
    color: #fff;
`

const Search2 = styled.div`
    order: 4;
    width: 100%;
    height: 40px;
    text-align: center;
    font-size: 1em
    margin: auto;
    display: block;
    color: #fff;
`
